.App {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  text-align: center;
  display: grid;
  grid-template:
    'header header' 3.4rem
    'nav main' 1fr
    'footer footer' 1.6rem /
      auto    1fr
  ;
}

a {
  color: var(--fontColor);
  text-decoration: none;
}

@media ( max-width: 600px ) {
  .App {
    grid-template:
      'header header' 3.4rem
      'nav nav' auto
      'main main' 1fr
      'footer footer' 2.8rem /
        auto    1fr
    ;
  }
}